input.switch12 {
    position: relative;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    width: 47px !important;
    height: 25px;
    border-radius: 30px;
    cursor: pointer;
    background: #ccc;
    transition: box-shadow 0.3s ease-in-out, padding 0.25s ease-in-out;
    transition-delay: 0.1s, 0s;
}

input.switch12:focus {
    border: 0;
    outline: 0;
}

input.switch12:checked {
    transition-delay: 0s, 0s;
    box-shadow: inset 0 0 0 2.62857em var(--primaryColor) !important;
    padding-left: 23px;
}

input.switch12:before {
    display: inline-block;
    height: 19px;
    width: 19px;
    margin: 3px;
    background-color: #fff;
    border-radius: 2.1em;
    transition: all 0.1s ease 0.1s;
}

input.switch12:before,
input.switch12:after {
    content: "";
    display: block;
}

input.switch12:active::before {
    transition: all 0.1s ease 0.05s;
}

