/* pulsation */
.icon {
    font-size: 16px;
    animation: pulsingText 2.7s infinite;
}

.icon:after {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    content: '';
    transition: transform 0.2s, opacity 0.2s;
}

.pulsing:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0px;
    content: '';
    animation: pulsing 2.7s infinite;
}

.textAreaToComment.chat {
    position: relative;
}

.textAreaToComment.chat>.errorCont {
    position: absolute;
    bottom: -5px;
}

.reportItem .headerNavLinks.active::after {
    all: unset;
}

.reportItem .headerNavLinks {
    justify-content: flex-start;
    color: #fff;
    padding: 0px;
}

.reportItems {
    position: absolute;
    left: 20px;
    display: none;
    background: var(--primaryColor);
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    min-width: 230px;
    width: fit-content;
    border-radius: 30px;
    top: 21px;
    padding: 10px !important;
    z-index: 4;
    height: fit-content;
    box-shadow: 0px 4px 10px rgb(0 0 0 / 25%);
}

.reportItems .reportItem {
    text-align: left;
