    margin-bottom: 2px;
    width: 100%;
    cursor: pointer;
    padding: 4px 10px;
}

.coverTypePost .preToNormal {
    word-wrap: break-word;
}

@keyframes pulsing {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    50% {
        transform: scale(1.5);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 0;
    }
}

@keyframes pulsingText {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.7);
    }

    100% {
        transform: scale(1);
    }
}

